<template>
    <section>
          <el-table :data="tableData" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%">
            <el-table-column type="selection" width="45">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column label="" prop="HJNF" sortable min-width="80">
            </el-table-column>
            <el-table-column label="" prop="HJJX" sortable min-width="80">
            </el-table-column>
        </el-table>





        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-button type="danger"  @click="dialogVisible = true ;prizeform=sels[0]" :disabled="this.sels.length===0"></el-button>
            <el-button  @click="dialogVisible = true"></el-button>

<el-dialog
  title=""
   :before-close="beforeClose"      :visible.sync="dialogVisible"
  width="30%">
      <el-form ref="form" :model="prizeform" label-width="80px">
    <el-form-item label="">
    <el-input v-model="prizeform.HJNF"></el-input>
    </el-form-item>
    <el-form-item label="">
    <el-input type="textarea" rows="5"  v-model="prizeform.HJJX"></el-input>
    </el-form-item>
    </el-form>
  <span slot="footer" class="dialog-footer">  
    <el-button type="primary" @click="addPrize"> </el-button>
  </span>
</el-dialog>
            <el-pagination layout="total, sizes, prev, pager, next, jumper" @current-change="handleCurrentChange" @size-change="handleSizeChange" :current-page="page.pageNum" :page-size="page.pageCount" :page-sizes="[10, 20, 50, 100]" :total="page.total" style="float:right;">
            </el-pagination>
        </el-col>

    </section>
</template>

<script>
import {savePrize, getPrizes,deletePrize } from '@/axios/axios'

export default {
    data() {
        return {
            prizeform:{},
            dialogVisible:false,
            listLoading: false, // 
            displayVal: true,// 
            sels: [],// 
             page: {
                total: 40,
                pageCount: 10,
                pageNum: 1
            },
            tableData: [
                {
                    ID:1,
                    HJNF:'20198',
                    HJJX:''
                   
                }, {
                  ID:2,
                    HJNF:'20199',
                    HJJX:''
                }, {
                  ID:3,
                    HJNF:'201910',
                    HJJX:''
                }]
        }
    },
    methods: {
        beforeClose(done){
          this.prizeform={}
          this.dialogVisible=false
        },
        addPrize:function(){
            let _that=this;
           savePrize(this.prizeform).then(function(response){
               _that.getListData;
               _that.dialogVisible=false
               _that.prizeform={}
               alert("")
            
           }).catch(()=>{

           })
        },
        // table
        getListData() {
               let _that=this;
               var url='?pageNum='+this.page.pageNum+'&pageCount='+this.page.pageCount
             getPrizes().then(function(response){
               _that.tableData=response.data.data.prize;
            }).catch((error)=>{
                 console.log(error)
            })
         
        },
        // 
        handleCurrentChange(val) {
            this.page.pageNum = val;
            this.getListData();
        },
        // 
        handleSizeChange(val) {
            this.page.pageCount = val;
            this.getListData();
        },
        // 
        selsChange: function(sels) {
            this.sels = sels;
        },
        // 
        batchRemove: function() {
            var ids=[];
            this.sels.forEach(element => {
                ids.push(element.ID)
            });
            let _that=this;
            deletePrize({ids:ids}).then(function(response){
             
             _that.getListData();
            }).catch((error)=>{
                 console.log(error)
            })
        },
    },
    mounted() {
        this.getListData();
    }
}
</script>

<style scoped>
.toolbar .el-select {
    /* border: 1px solid red; */
    margin-right: 10px;
    margin-bottom: 0;
}

.monthClass .el-select {
    width: 62px;
}

.dayClass .el-select {
    width: 62px;
}
</style>



